:root {
  --primary-bg-color: #822626;
  --left-column-bg-color: #11fc2c;
  --right-column-bg-color: #f4e808;
  --header-footer-bg-color: #333;
  --text-color: #fff;
  --font-family: "Taviraj", serif;
  --main-content-width: 60%;
  --column-width: 20%;
  --responsive-breakpoint: 768px; /* Define a CSS variable for responsive breakpoint */
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

h1 {
  font-family: var(--font-family);
  font-weight: 600;
  font-style: italic;
}

.main-content {
  display: flex;
  background-color: var(--primary-bg-color);
  flex-grow: 1; /* Ensures that the main content area flexes with the viewport */
  align-items: stretch; /* Ensures child elements fill the container height */
  width: var(--main-content-width);
  margin: 0 auto; /* Centres the main content horizontally */
}

.left-column, .right-column {
  flex-basis: var(--column-width); /* Using CSS variable for consistency and easy maintenance */
  min-height: 100%; /* Ensures side columns stretch to match the parent container's height */
}

.left-column {
  background-color: var(--left-column-bg-color);
}

.right-column {
  margin-left: auto; /* Aligns the right column to the right */
  background-color: var(--right-column-bg-color);
}

header, footer {
  background-color: var(--header-footer-bg-color);
  color: var(--text-color);
  padding: 10px 0;
  text-align: center;
}

footer {
  align-self: stretch; /* Stretches the footer to match its parent's width */
}

/* Responsive Design Update */
@media (max-width: var(--responsive-breakpoint)) {
  .left-column, .right-column {
    display: none; /* Hides side columns on smaller screens for better readability */
  }
  .main-content {
    width: 100%; /* Allows main content to fill the screen on smaller devices */
  }
}
